var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美膚光療")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("最新第四代Q+音波拉提")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"最新第四代Q+音波拉提","titleEn":"ULTRACEL-Q"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/banner.png")))+" alt=\"最新第四代Q+音波拉提 Banner\"></picture> <h2>為什麼會老化?</h2> <h3>老化，是每個人一生都必須面對的課題！</h3> <p>老化的原因可分為兩類，一是先天基因的影響，二是後天外在因素所引起的 (如自由基、日曬、抽菸等、大氣污染環境因素、電磁輻射、生活方式與飲食不均)</p> <p>老化是每天都在發生的事情，現代人生活忙碌工作壓力大，有時外表與心境老得比實際年齡快，以膠原蛋白來說就是以每年 2% 的速度快速消失，排列變得不整齊，使得肌膚的厚度和彈性變低，會讓肌膚產生細紋。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/1.jpg")))+" alt=\"心情要鬆，皮膚千萬不能鬆！\"> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/1.jpg")))+" alt=\"心情要鬆，皮膚千萬不能鬆！\"></picture> <p>老化的皮膚， 表皮會變的比較薄，膚色也會變的不均勻，皮膚變得乾燥缺乏彈性，膚色暗沉，深層的皮下脂肪層，肌肉筋膜層也會因為老化或地心引力拉扯的關係，使得脂肪流失逐漸位移，筋膜層的支撐力不足，造成臉上開始出現皺紋，淚溝、法令紋、雙頰凹陷，眼皮也會開始下垂，下顎曲線也因鬆弛顯得模糊，嘴邊肉更是讓人盡顯老態。</p> <p>想要抗老回春，就要先了解老化的原因為何，隨著醫美技術進步，現在不用透過侵入性手術，忍受皮肉之痛，</p> <p>只要利用午休零碎時間進廠保養，就能輕鬆保有青春的容貌與窈窕的身體曲線，何樂而不為呢？</p></div> <div class=\"article-container\"><h2>最新第四代Q+音波拉提 快速抗老新趨勢 拉緊妳的輪廓線</h2> <iframe src=\"https://www.youtube.com/embed/9bEzeiJkFx8\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\"></iframe> <p>相信平常有接觸醫學美容的愛美族群對音波拉提不算陌生，甚至對音波帶來的功效無法抗拒，緊實輪廓線，收緊鬆弛的肌膚、刺激膠原蛋白增生，讓肌齡瞬間年輕10歲。</p> <p>“婕希”聚焦超音波系統（ULTRAcel Q+）是台灣FDA核准的、非侵入性、不開刀的深層拉提治療，用於治療鬆弛的雙頰、抬頭紋、法令紋、雙下巴、眼周細紋、身體橘皮、肥胖紋等部位，甚至是提眉亮眼，探頭選擇多，8種探頭，適用於臉部及身體的音波系統，也就是現今醫美市場【第四代Q+音波拉提】。</p></div> <div class=\"article-container\"><h2>【第四代Q+音波拉提】專為亞洲人設計的音波系統</h2> <p>精確的治療效果使其療程在韓國引起廣大迴響，並且許多名媛貴婦及女性指定作為定期保養的療程項目之一，美仕媞時尚醫美診所引進【第四代Q+音波拉提】，相在未來將席捲全台。</p> <p>HIFU 相比市面上其他機頭細小，能快速集中地打出相同發數，比一般HIFU更短時間發揮療效，也因探頭面積小，可視角增大，增加治療安全性。</p> <p>【第四代Q+音波拉提】另一項特色是可以針對患者鬆弛程度及耐受度作能量調整，在諮詢過程中可依照患者的需求客製化適合的操作方法，想要達到理想的效果，選擇服務良好的診所是考量之一，挑選適合自己的音波儀器，雖然在台灣起步較晚，但在韓國有非常高的市佔率，相信在台灣也會造成一股旋風，一台好的儀器也須搭配醫師的專業操作及經驗，針對不同的患者，正確的診斷每個部位要施打的探頭及條數，才能確實帶來完美的改變，這就是為什麼不同的醫師操作會有很大的差異，例如醫師推估需要500條，什麼探頭打幾條，每個患者的狀況都不同，並沒有一樣標準化的療程，若是醫師沒有準確的判斷及臨床經驗，就無法達到自己理想的效果。</p></div> <div class=\"article-container\"><h2>注意事項</h2> <ul><li>- 紅斑：治療區域可能在治療之後立即出現紅斑。通常在治療數小時後消失。但是可能在治療之後維持 48 小時。</li> <li>- 水腫：治療區域可能在治療之後出現輕微水腫。但是此症狀會在治療之後 48 小時內消失。</li> <li>- 疼痛：進行治療時患者可能暫時感覺不舒服。</li> <li>- 瘀青：治療區域可能在治療之後出現輕微瘀青。但是此症狀會在治療之後 2~7 天內消失</li></ul></div> <div class=\"article-container\"><h2>不適合進行音波拉提</h2> <ul><li>- 有皰疹病毒感染的患者</li> <li>- 對局部麻醉產生皮膚過敏的患者</li> <li>- 有出血性疾病或其他止血功能障礙的患者</li> <li>- 孕婦及嬰兒</li> <li>- 蟹足腫皮膚</li> <li>- 皮膚癌</li> <li>- 乳房植入物</li> <li>- 治療區域有開放性傷口</li> <li>- 有機械植體或植入式電子裝置者</li></ul></div> <div class=\"article-container\"><h2>本產品尚未評估用於以下患者</h2> <ul><li>- 自體免疫性疾病</li> <li>- 糖尿病</li> <li>- 癲癇病</li> <li>- 急性疾病</li> <li>- 高血壓</li> <li>- 皮膚炎</li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例1.png")))+" alt=\"案例1\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例2.png")))+" alt=\"案例2\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例3.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例3.png")))+" alt=\"案例3\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例4.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例4.png")))+" alt=\"案例4\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例5.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例5.png")))+" alt=\"案例5\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例6.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例6.png")))+" alt=\"案例6\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/preventiveMedicine/ultracel-q/案例7.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/ultracel-q/案例7.png")))+" alt=\"案例7\"></picture> <p>療程效果會因個人體質而有所差異</p></div>")],2)}
